.contacts {
  background: @white;

  @media (min-width: @desktop-width) {
    position: relative;
  }
}

.contacts-block {
  position: relative;
  display: flex;
  text-align: left;
  align-items: center;
  padding: 26px 0;

  @media (min-width: @tablet-width) {
    gap: 295px;
    padding: 56px 40px;
  }

  @media (min-width: @desktop-width) {
    top: 95px;
    gap: 87px;
    width: 565px;
    height: 198px;
    padding: 56px 60px 56px 80px;
    background-color: @white;
    z-index: 1;
  }

}

.contacts__title {
  .title();
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 26px;
    width: 170px;
    text-align: left;
  }
}

.contacts__text {
  text-align: left;

  @media (min-width: @tablet-width) {
    width: 160px;
    text-align: right;
  }

  @media (min-width: @desktop-width) {
    width: 175px;
    text-align: left;
  }
}

.map {
  position: relative;
  width: 320px;
  height: 362px;

  @media (min-width: @tablet-width) {
    width: 768px;
    height: 400px;
  }

  @media (min-width: @desktop-width) {
    width: 1280px;
    top: -198px;
    margin-bottom: -203px;
  }
}

