.additional:last-child {
  margin-bottom: 55px;

  @media (min-width: @desktop-width) {
    margin-bottom: 85px;
  }
}

.additional__content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
  }
}

.additional__value {
  margin-left: 50px;

  @media (min-width: @desktop-width) {
    margin-right: 40px;
  }
}

.order-button {
  width: 100%;
  margin-bottom: 19px;

  @media (min-width: @tablet-width) {
    margin-bottom: 25px;
  }

  @media (min-width: @desktop-width) {
    font-size: 16px;
    padding: 7px 26px;
    margin: 0;
  }
}
